import Vue from 'vue'
import App from '@/App'
import Router from 'vue-router'
import 'html5-reset/assets/css/reset.css'

import testProduct from '@/view/crowdTest/test-product.vue'; //众测产品


Vue.use(Router)

const router = new Router({
  // mode: 'history',
  // base: '/test-product.html',
  routes: [{
    path: '/',
    name: 'testProduct',
    component: testProduct
  }]
})


Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: {
    App
  },
  template: '<App/>'
})



